const connGraph = (data, title, size) => ({
  title: {
    text: title,
    left: 'center',
    top: '5%',
  },
  tooltip: {
    trigger: 'item',
    formatter: (params) => {
      const { data } = params.data;
      return '<div style="max-width: 200px; display: inline-block; white-space: pre-wrap">' +
        `<div>${data?.clusterNum}:</div>` +
        `<div>${data?.time}</div>` +
        `<div style="max-height: 150px; overflow: hidden; text-overflow: ellipsis">${data?.summary}</div>` +
      '</div>';
    },
    triggerOn: 'mousemove',
  },
  series: [
    {
      type: 'tree',
      data: [data],
      roam: true,
      left: '10%',
      right: '10%',
      symbol: 'circle',
      symbolSize: 30,
      label: {
        position: 'right',
        left: '50%',
        verticalAlign: 'middle',
        align: 'left',
        fontSize: 18,
        fontWeight: 'bolder',
      },

      leaves: {
        label: {
          position: 'right',
          verticalAlign: 'middle',
          align: 'left',
        },
      },
      emphasis: {
        focus: 'descendant',
      },
      initialTreeDepth: [-1],
      expandAndCollapse: true,
      animationDuration: 550,
      animationDurationUpdate: 750,

    },
  ],
});

export default connGraph;
