import requests from "../../requests";
import qs from 'qs';

const getProgrammeRegionLayout = async (fid, startPublishedDay, endPublishedDay) => {
  const params = {
    fid,
    startPublishedDay,
    endPublishedDay,
  };
  const url = encodeURI(`${requests.getProgrammeRegionLayout.url}?${qs.stringify(params)}`);
  const response = await fetch(url, { method: requests.getProgrammeRegionLayout.method });
  const rawResult = response.status === 200 ? await response.json() : {};

  const regionMap = new Map();
  // eslint-disable-next-line no-lone-blocks
  {
    regionMap.set(11, '');
    regionMap.set(12, '');
    regionMap.set(13, '');
    regionMap.set(14, '');
    regionMap.set(15, '');
    regionMap.set(21, '');
    regionMap.set(22, '');
    regionMap.set(23, '');
    regionMap.set(31, '');
    regionMap.set(32, '');
    regionMap.set(33, '');
    regionMap.set(34, '');
    regionMap.set(35, '');
    regionMap.set(36, '');
    regionMap.set(37, '');
    regionMap.set(41, '');
    regionMap.set(42, '');
    regionMap.set(43, '');
    regionMap.set(44, '');
    regionMap.set(45, '');
    regionMap.set(46, '');
    regionMap.set(50, '');
    regionMap.set(51, '');
    regionMap.set(52, '');
    regionMap.set(53, '');
    regionMap.set(54, '');
    regionMap.set(61, '');
    regionMap.set(62, '');
    regionMap.set(63, '');
    regionMap.set(64, '');
    regionMap.set(65, '');
    regionMap.set(71, '');
    regionMap.set(81, '');
    regionMap.set(91, '');
  }

  const regionLayout = Object.keys(rawResult)
    .map(key => parseInt(key.slice(-2), 10))
    .map(key => ({
      name: regionMap.get(key),
      value: rawResult[`from${key}`],
    }));
  let totals = 0;
  let cnt = 0;
  let min = regionLayout[0]?.value || 0;
  let max = 0;
  regionLayout.forEach((item) => {
    totals += item.value;
    cnt += 1;
    if (item.value < min) min = item.value;
    if (item.value > max) max = item.value;
  });
  return {
    regions: regionLayout,
    min,
    max,
  };
};

export default getProgrammeRegionLayout;
