import React from 'react';
import Home from '../Home/Home';
import Overall from '../Overall/Overall';
import Programme from '../Programme/Programme';
import Admin from '../Admin/Admin';
import View from '../View/View';

const getRoutes = (userType) => {
  const routes = [
    { key: 'home', link: '/home', label: '', component: Home },
    { key: 'search', link: '/search', label: '', component: Overall },
    { key: 'monitor', link: '/monitor', label: '', component: Programme },
    { key: 'view', link: '/view', label: '', component: View },
  ];
  if (userType === 'admin') routes.push({ key: 'admin', link: '/admin', label: '', component: Admin });
  return routes;
};

export default getRoutes;
