import React from 'react';
import { connect } from 'react-redux';
import { Input, Switch } from 'antd';
import AutofitWrap from '../../common/AutofitWrap/AutofitWrap';
import { actions } from '../../../redux/actions';
import DataList from '../../common/DataList/DataList';
import './Alert.scss'
import Echart from "../../common/Echart/Echart";

const sensiLayout = [
  { name: '', label: '', value: 376 },
  { name: '', label: '', value: 91 },
  { name: '', label: '', value: 61 },
  { name: '', label: '', value: 66 },
  { name: '', label: '', value: 57 },
  { name: '', label: '', value: 41 },
];
const emotionTrendLayout = {
  'yAxis': ['1993-', '1980-', '2003-', '1981-', '1984-', '2007-'],
  'xAxis': [
    { 'name': '', 'label': '', 'value': [14, 10, 6, 12, 13, 9] },
    { 'name': '', 'label': '', 'value': [16, 7, 14, 11, 6, 19] },
    { 'name': '', 'label': '', 'value': [15, 4, 12, 14, 15, 9] },
    { 'name': '', 'label': '', 'value': [6, 14, 8, 11, 3, 3] },
    { 'name': '', 'label': '', 'value': [7, 15, 12, 19, 3, 2] },
    { 'name': '', 'label': '', 'value': [9, 5, 17, 6, 16, 14] }],
};

class Alert extends React.Component {
  formatEmotionTrendLayout = (raw) => {
    const yAxis = [];
    yAxis.push({
      name: '',
      data: raw.xAxis[0].value,
    });
    const negData = raw.xAxis[1].value.map((item, index) => (
      item + raw.xAxis[2].value[index] + raw.xAxis[3].value[index]
    ));
    yAxis.push({
      name: '',
      data: negData,
    });
    yAxis.push({
      name: '',
      data: raw.xAxis[5].value,
    });
    console.log(yAxis);
    return {
      xAxis: raw.yAxis,
      yAxis,
    };
  };

  render() {
    return (
      <div
        padding={200}
        minHeight={550}
        className="alert-wrap"
      >
        <div className="alert-container">
          <div className="graphs">
            <div className="left-graph">
              <Echart
                title=""
                type="basicLine"
                data={[]}
              />
            </div>
            <div className="right-graph">
              <div className="top-graph">
                <Echart
                  title=""
                  type="stackLine"
                  data={this.formatEmotionTrendLayout(emotionTrendLayout)}
                />
              </div>
              <div className="bottom-graph">
                <Echart
                  title=""
                  type="defaultPie"
                  data={sensiLayout}
                />
              </div>
            </div>
          </div>
          <div>
            
            <DataList
              data={[]}
            />
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  userName: state.userName,
  curProgramme: state.curProgramme,
});
const mapDispatchToProps = {
  onProgrammeChange: actions.onProgrammeChange,
};

export default connect(mapStateToProps, mapDispatchToProps, null, { forwardRef: true })(Alert);
