import React from 'react';
import { Table } from "antd";
import {Switch} from "antd/es";

class UserList extends React.Component {
  constructor() {
    super();
    this.columns = [
      {
        title: '',
        dataIndex: 'userName',
        key: 'userName',
      },
      {
        title: '',
        dataIndex: 'userType',
        key: 'userType',
        render: (field) => (field === 'admin' ? '' : ''),
      },
      {
        title: '',
        dataIndex: 'phone',
        key: 'phone',
      },
      {
        title: '',
        dataIndex: 'email',
        key: 'email',
      },
      {
        title: '',
        key: 'action',
        render: () => <Switch/>
      },
    ];
  }

  render() {
    const { users } = this.props;
    return (
      <Table
        dataSource={users}
        columns={this.columns}
        pagination={false}
      />
    );
  }
};

export default UserList;