import React from 'react';
import { Form, Input, Button } from 'antd';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import { actions } from '../../../redux/actions';
import '../Authority.scss';
import register from '../../../services/request/auth/register';

class Register extends React.Component {
  constructor(props) {
    super(props);
    this.layout = {
      labelCol: { span: 6 },
      wrapperCol: { span: 16 },
    };
    this.tailLayout = {
      wrapperCol: { offset: 6, span: 16 },
    };
  }

  handleSubmit = async (data) => {
    const result = await register(data.userName, data.password, data.email, data.phone);
    if (result.register !== 1) alert('');
    else alert('!');
  };

  handleReject = () => {};

  render() {
    return (
      <Form
        className="login-form"
        {...this.layout}
        onFinish={this.handleSubmit}
        onFinishFailed={this.handleReject}
      >
        <Form.Item
          label=""
          name="userName"
          rules={[{ required: true, message: '!' }]}
        >
          <Input />
        </Form.Item>
        <Form.Item
          label=""
          name="password"
          rules={[{ required: true, message: '!' }]}
        >
          <Input.Password />
        </Form.Item>
        <Form.Item
          label=""
          name="confirm"
          rules={[
            { required: true, message: '' },
            ({ getFieldValue }) => ({
              validator(_, value) {
                if (!value || getFieldValue('password') === value) { return Promise.resolve(); }
                return Promise.reject(new Error(''));
              },
            }),
          ]}
        >
          <Input.Password />
        </Form.Item>
        <Form.Item
          label=""
          name="phone"
          rules={[{ required: true, message: '!' }]}
        >
          <Input />
        </Form.Item>
        <Form.Item
          label=""
          name="email"
          rules={[{ required: true, message: '!' }]}
        >
          <Input />
        </Form.Item>
        <Form.Item
          {...this.tailLayout}
        >
          <Button
            type="primary"
            htmlType="submit"
          >
            
          </Button>
        </Form.Item>
      </Form>
    );
  }
}

const mapStateToProps = (state) => ({});
const mapDispatchToProps = {
  onAuthChange: actions.onAuthChange,
};

export default withRouter(connect(mapStateToProps, mapDispatchToProps, null, { forwardRef: true })(Register));
