const basicLine = (data, title, size) => {
  return {
    title: {
      text: title,
      left: 'center',
      top: '5%',
    },
    xAxis: {
      type: 'category',
      data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun']
    },
    yAxis: {
      type: 'value',
    },
    series: [{
      data: [150, 230, 224, 218, 135, 147, 260],
      type: 'line',
    }],
  };
};

export default basicLine;